<template>
  <div class="w-full flex">
    <div class="mx-auto mt-24">
      <div class="flex w-full">
        <div class="mx-auto text-center space-y-8">
          <img src="@/assets/lock.svg" class="w-32 mx-auto" alt="data-match-lock" />
          <div class="text-lg">
            <div>Data match in progress. This can take several minutes.</div>
            <div>Please check back later or refresh the page below.</div>
          </div>
          <Button view="outlined" color="teal" class="lg:inline-block block" @click="checkUpdate" :loading="loading">
            <span class="material-icons">refresh</span> Refresh
          </Button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { defineComponent, ref } from 'vue'
import { useLogin } from 'composable/user'
import debounce from 'utils/debounce'
import Button from 'bbUi/components/button/button.vue'

export default defineComponent({
  name: 'DataMatchProcess',
  components: {
    Button
  },
  setup() {
    const { loginStatus, datamatchStatus } = useLogin()
    const loading = ref(false)

    const checkUpdate = debounce(async () => {
      loading.value = true
      await loginStatus().finally(() => loading.value = false)
    }, 300)

    return {
      datamatchStatus,
      checkUpdate,
      loading
    }
  }
})
</script>
